.images {
    margin-left: 0 !important;
}

.polaroid_images_a {
    background: white;
    /*  display: inline; */
    float: left;
    margin: 0 15px 30px;
    padding: 0px 0px 25px 0;
    text-align: center;
    text-decoration: none;
    -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
    box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
    -webkit-transition: all .15s linear;
    -o-transition: all .15s linear;
    transition: all .15s linear;
    z-index: 0;
    position: relative;
}

.polaroid_images_a:after {
    color: #333;
    font-size: 20px;
    content: attr(title);
    position: relative;
    top: 15px;
}

.polaroid_images_img {
    display: block;
    width: inherit;
}

.polaroid_images_a:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    z-index: 10;
    -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, .7);
    box-shadow: 0 10px 20px rgba(0, 0, 0, .7);
}

.image-slide {
    height: 100%;
    width: 100%;
    -webkit-transition: background-image .3s ease-in-out;
    -o-transition: background-image .3s ease-in-out;
    transition: background-image .3s ease-in-out;
}

.slide-arrow {
    position: relative;
    color: black;
    cursor: pointer;
    font-size: 2rem;
    margin-top: 10rem;
}

.slide-arrow.right {
    float: right;
}

.slide-arrow.left {
    float: left;
}
@media only screen and (max-width: 600px) {
    .slide-arrow {
        position: absolute;
        z-index: 20;
        display:none !important;
    };
    .slide-arrow.right {
        float: right;
        left: calc(100% - 10%);
    }
    
    .left {
        left:0;
    }
}
.skillImage {
    height: 20rem;
    width: 22rem;
    -o-object-fit: contain;
       object-fit: contain;
    border-radius: 4px;
    opacity: 0.7;
    padding: 1rem;
}


